<template>
  <div class="play">
    <div class="header">
      <div class="title">
        <router-link to="/">
          <i class="el-icon-refresh-left left"></i>
        </router-link>
        <router-link to="/search">
          <i class="el-icon-search right"></i>
        </router-link>
      </div>
    </div>
    <div class="song-info">
      <div class="song-info-img">
        <img :src="getUrl" alt="" />
      </div>
      <p class="music-info">
        {{ $route.params.name }}
      </p>
      <div class="song">
        <audio controls loop :src="playerData.url">
         
        </audio>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      playerData: {},
    };
  },
  mounted() {
    this.$api
      .getPlayer({
        id: this.$route.params.id,
      })
      .then((res) => {
        this.playerData = res.data[0];
      });
  },
  computed: {
    getUrl() {
      //解码
      return decodeURIComponent(this.$route.params.image);
    },
  },
};
</script>

<style scoped>

.header {
  padding: 20px;
}
.title {
  display: flex;
  justify-content: space-between;
}
.music-info {
  padding: 20px;
  font-size: 20px;
  /* 取消换行 */
  white-space: nowrap;
  /* 隐藏溢出部分 */
  overflow: hidden;
  /* 超出用...表示 */
  text-overflow: ellipsis;
  text-align: center;
}

i {
  font-size: 30px;
}

.song-info {
  padding: 10px;
}

.song-info-img {
  margin: 10px auto;
  width: 60%;
  box-shadow: 0 0 10px 0 rgba(228, 10, 10, 0.6);
}
.song {
  width: 100%;
  text-align: center;
}
</style>